import React, { useState, useEffect } from 'react';
import { Image } from 'semantic-ui-react';
import { map } from 'lodash';
import { Link } from 'react-router-dom';
import { useQuery } from '@apollo/client';
import { GET_STORYS_FOLLOWERS } from '../../../gql/story';
import { GET_PUBLICATIONS_FOLLOWERS } from '../../../gql/publication';
import Action from '../../Modal/ModalPublication/Action';
import Comments from '../../Publications/Comments';
import CommentForm from '../../Modal/ModalPublication/CommentForm';
import ImageNoFound from '../../../assets/png/avatar.png';
import ModalPublication from '../../Modal/ModalPublication';
import ReactPlayer from 'react-player';
import StoryUsers from '../StoryUsers';
import './Feed.scss';

export default function Feed() {
    const [showModal, setShowModal] = useState(false);
    const [publicationSelect, setPublicationSelect] = useState(null)
    
    const {
        data: dataPublication, 
        loading: loadingPublication, 
        startPolling: startPollingPublication, 
        stopPolling: stopPollingPublication
    } = useQuery(GET_PUBLICATIONS_FOLLOWERS);
    
    const {
        data: dataStory, 
        loading: loadingStory, 
        startPolling: startPollingStory, 
        stopPolling: stopPollingStory
    } = useQuery(GET_STORYS_FOLLOWERS);
    
    useEffect(() => {
        startPollingPublication(1000);
        return () => {
            stopPollingPublication();
        }
    }, [startPollingPublication, stopPollingPublication]);

    useEffect(() => {
        startPollingStory(1000);
        return () => {
            stopPollingStory();
        }
    }, [startPollingStory, stopPollingStory]);
    
    if(loadingStory) return null;
    const {getStorysFollowers} = dataStory;

    if(loadingPublication) return null;
    const {getPublicationsFollowers} = dataPublication;

    const openPublication = (publication) => {
        setPublicationSelect(publication); 
        setShowModal(true);
    }

    return (
        <>
            <div className="feed">
                <StoryUsers className="feed__box-story" getStorysFollowers={getStorysFollowers} />
                
                {map(getPublicationsFollowers, (publication, index) => (
                    <div className="feed__box" key={index}>
                        <Link to={`/${publication.idUser.username}`}>
                            <div className="feed__box-user">
                                <Image src={publication.idUser.avatar || ImageNoFound} avatar />
                                <span>{publication.idUser.name}</span>
                            </div>
                        </Link>

                        {publication.typeFile === "image" ? (
                            <div 
                                className="feed__box-photo"
                                onClick={() => openPublication(publication)}
                            >
                                <Image
                                    src={publication.file}
                                />
                            </div>
                        ) : (
                            <ReactPlayer  
                                className="feed__box-user-video"
                                url = {publication.file}
                                width="100%"
                                controls
                                muted
                            />
                        )}
                        <div>
                            <div className="feed__box-actions">
                                <Action publication={publication} />
                            </div>
                            <div className="feed__box-comments">
                                <Comments publication={publication} />
                            </div>
                        </div>
                        <div className="feed__box-form">
                            <CommentForm publication={publication} />
                        </div>
                    </div>
                ))}
            </div>
            {showModal && (
                <ModalPublication
                    show={showModal} 
                    setShow={setShowModal}
                    publication={publicationSelect}
                />
            )}
        </>
    )
}
