import React, { useState } from 'react';
import { Icon, Image } from 'semantic-ui-react';
import { map } from 'lodash';
import { Link } from 'react-router-dom';
import useAuth from '../../../hooks/useAuth';
import { useQuery } from '@apollo/client';
import { GET_USER } from '../../../gql/user';
import ModelUploadStory from '../../Modal/ModalUploadStory';
import ImageNoFound from '../../../assets/png/avatar.png';
import './StoryUsers.scss';

export default function StoryUsers(props) {
    const { getStorysFollowers } = props;
    const [showModal, setShowModal] = useState(false);
    const { auth } = useAuth();
    const { data, loading, error } = useQuery(GET_USER, {
        variables: { username: auth.username },
    });

    if(loading || error) return null;
    if(error) return <ImageNoFound />;

    const { getUser } = data;

    const onAddStory = () => {
      setShowModal(true)
    }

    return (
        <>
            <div className="slider-frame">
                <Link to="/storys">
                    <Icon className="btn next" circular inverted name="arrow right" size="large" />
                </Link>

                <div className="story-users">
                    <div className="time-line-story">
                        <div className="time-line-story__add" onClick={onAddStory}>
                            <Image className="img-story-add" src={getUser.avatar ? getUser.avatar : ImageNoFound} />
                            <Icon 
                                name="plus"
                                size="big"
                            />
                            <span>Crear historia</span>
                        </div>

                        {map(getStorysFollowers, (story, index) => (
                            <div className="time-line-story__view" key={index}>
                                <div className="btn prev"></div>
                                <div className="btn next"></div>
                                <div>
                                    <Image className="img-avatar" src={story.idUser.avatar || ImageNoFound} avatar />
                                </div>
                                <Image className="img-story" src={story.file} />
                            </div>
                        ))}
                    </div>
                </div>
            </div>
            <ModelUploadStory show={showModal} setShow={setShowModal} />
        </>
    )
}
