import React from 'react';
import { Form, Button } from 'semantic-ui-react';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import './RegisterForm.scss';
import { useMutation } from '@apollo/client';
import { REGISTER } from '../../../gql/user';
import { toast } from 'react-toastify';

export default function RegisterForm(props) {
    const { setShowLogin } = props;
    const [register] = useMutation(REGISTER);

    const formik = useFormik({
        initialValues: initialValues(),
        validationSchema: Yup.object({
            name: Yup.string()
                .required("El nombre es obligatorio!"),
            username: Yup.string()
                .matches(/^[a-zA-z0-9-]*$/, "Nombre de usuario no puede tener espacios!")
                .required("Nombre de usuario es requerido!"),
            email: Yup.string()
                .email("El email es invalido!")
                .required("Email es requerido!"),
            password: Yup.string()
                .required("La contraseña es requerido!")
                .oneOf([Yup.ref("repeatPassword")], "Las contraseñas no son iguales"),
            repeatPassword: Yup.string()
                .required("La contraseña es requerido!")
                .oneOf([Yup.ref("password")], "Las contraseñas no son iguales"),
        }),
        onSubmit: async (formData) => {
            try {
                const newUser = formData;
                delete newUser.repeatPassword;

                await register({
                    variables: {
                        input: newUser,
                    }
                });

                toast.success("Bienvenidos a la red social de venezuela");
                setShowLogin(true)
            } catch (error) {
                toast.error(error.message);
            }
        }
    })

    return (
        <div>
            <h2 className="register-form-title">Registrate para ver fotos y videos de tus amigos.</h2>
            <Form className="register-form" onSubmit={formik.handleSubmit}>
                <Form.Input 
                    type="text" 
                    placeholder="Nombre y Apellidos" 
                    name="name" 
                    value={formik.values.name} 
                    onChange={formik.handleChange}
                    error={formik.errors.name}
                />
                <Form.Input 
                    type="text" 
                    placeholder="Nombre de usuario" 
                    name="username" 
                    value={formik.values.username} 
                    onChange={formik.handleChange}
                    error={formik.errors.username && true}
                />
                <Form.Input 
                    type="text" 
                    placeholder="Correo electronico" 
                    name="email" 
                    value={formik.values.email} 
                    onChange={formik.handleChange}
                    error={formik.errors.email && true}
                />
                <Form.Input 
                    type="password" 
                    placeholder="Contraseña" 
                    name="password" 
                    value={formik.values.password} 
                    onChange={formik.handleChange}
                    error={formik.errors.password && true}
                />
                <Form.Input 
                    type="password" 
                    placeholder="Repetir contraseña" 
                    name="repeatPassword" 
                    value={formik.values.repeatPassword} 
                    onChange={formik.handleChange}
                    error={formik.errors.repeatPassword && true}
                />
                
                <Button type="submit" className="btn-submit">Registrarse</Button>
            </Form>
        </div>
    )
}

function initialValues(){
    return {
        name: "",
        username: "",
        email: "",
        password: "",
        repeatPassword: ""
    };
}