import { gql } from '@apollo/client';

export const ADD_STORY = gql`
    mutation addStory($file: Upload) {
        addStory(file: $file){
            status
            urlFile
        }
    }
`;

export const GET_STORYS_FOLLOWERS = gql`
    query getStorysFollowers{
        getStorysFollowers{
        id
        idUser {
            name
            username
            avatar
        }
        file
        typeFile
        createAt
        }
    }
`;