import React from 'react';
import { Button } from 'semantic-ui-react';
import { useHistory } from 'react-router-dom';
import { useApolloClient } from '@apollo/client';
import useAuth from '../../../hooks/useAuth';
import PasswordForm from '../PasswordForm';
import EmailForm from '../EmailForm';
import DescriptionForm from '../DescriptionForm';
import WebSiteForm from '../WebSiteForm';
import './SettingsForm.scss';

export default function SettingsForm(props) {
    const {
        setShowModal, 
        setTitleModal, 
        setChildrenModal, 
        getUser, 
        auth, 
        refetch
    } = props;
    
    const { logout } = useAuth();
    const history = useHistory();
    const client = useApolloClient();

    const onChangePassword = () => {
        setTitleModal(
            'Cambiar contraseña'
        );
        setChildrenModal(
            <PasswordForm logout={onLogout} />
        )
    }

    const onChangeEmail = () => {
        setTitleModal('Cambiar Email');
        setChildrenModal(
            <EmailForm 
                setShowModal={setShowModal} 
                currentEmail={getUser.email} 
                refetch={refetch}
            />
        )
    }

    const onChangeDescription = () => {
        setTitleModal('Cambiar Descripción');
        setChildrenModal(
            <DescriptionForm 
                setShowModal={setShowModal}
                currentDescription={getUser.description}
                refetch={refetch}
            />
        )
    }

    const onChangeWebSite = () => {
        setTitleModal('Cambiar Sitio Web');
        setChildrenModal(
            <WebSiteForm 
                setShowModal={setShowModal}
                currentWebSite={getUser.website}
                refetch={refetch}
            />
        )
    }

    const onLogout = () => {
        client.clearStore();
        logout();
        history.push('/');
    }

    return (
        <div className="setting-from">
            <Button onClick={onChangePassword}>Cambiar Contraseña</Button>
            <Button onClick={onChangeEmail}>Cambiar Email</Button>
            <Button onClick={onChangeDescription}>Cambiar Descripción</Button>
            <Button onClick={onChangeWebSite}>Cambiar Sitio Web</Button>
            <Button onClick={onLogout}>Cerrar Sessión</Button>
            <Button onClick={() => {setShowModal(false)}}>Cancelar</Button>
        </div>
    )
}
