import React, { useState, useCallback } from 'react';
import { Modal, Icon, Button, Dimmer, Loader, Image } from 'semantic-ui-react';
import { toast } from 'react-toastify';
import { useDropzone } from 'react-dropzone';
import { useMutation } from '@apollo/client';
import { ADD_STORY } from '../../../gql/story';
import './ModalUploadStory.scss';

export default function ModalUploadStory(props) {
    const { show, setShow } = props;
    const [fileUpload, setFileUpload] = useState(null);
    const [isLoading, setIsLoading] = useState(false);
    const [addStory] = useMutation(ADD_STORY);

    const onDrop = useCallback((acceptedFile) => {
        const file = acceptedFile[0];
        
        setFileUpload({
            type: file.type,
            file,
            preview: URL.createObjectURL(file),
        })
    });

    const { getRootProps, getInputProps } = useDropzone({
        accept: "image/jpeg, image/png, video/*",
        noKeyboard: true,
        multiple: false,
        onDrop
    });

    const onPublish = async () => {
        try {
            setIsLoading(true);
            const result = await addStory({
                variables: {
                    file: fileUpload.file
                },
            });
            const { data } = result;

            if(!data.addStory.status){
                toast.warning('Error en la publicación');
                isLoading(false);
            }else{
                toast.success('Has registrado una nueva historia!!!');
                onClose();
            }
        } catch (error) {
            console.log(error)
        }
    };

    const onClose = () => {
        setIsLoading(false);
        setFileUpload(null);
        setShow(false);
    };

    return (
        <Modal size="small" open={show} onClose={onClose} className="modal-upload-story">
            {!fileUpload && (
                <div {...getRootProps()} className="dropzone" style={ fileUpload && { border:0 } }>
                    <>
                        <Icon name="cloud upload" />
                        <p>Arrastrar la foto o video que quieres publicar</p>
                        <input {...getInputProps()} />
                    </>
            </div>
            )}

            {fileUpload && (
                <div className="image" >
                    <Image src={fileUpload.preview} />
                </div>
            )}

            {fileUpload && (    
                <Button className="btn-upload btn-action" onClick={onPublish}>
                    Publicar
                </Button>
            )}

            {isLoading &&  (
                <Dimmer active className="publishing">
                    <Loader />
                    <p>Publicando...</p>
                </Dimmer>
            )}
        </Modal>
    )
}
