import React from 'react';
import { Image } from 'semantic-ui-react';
import { map } from 'lodash';
import { useQuery } from '@apollo/client';
import { GET_IS_FOLLOWEDS } from '../../../gql/follow';
import ImageNoFound from '../../../assets/png/avatar.png';
import './UsersIsFolloweds.scss';

export default function UsersIsFolloweds() {
    const { data, loading } = useQuery(GET_IS_FOLLOWEDS);

    if(loading) return null;
    const { getIsFolloweds } = data;
 
    return (
        <div className="users-is-not-followeds">
            <h3>Amigos</h3>
            {map(getIsFolloweds, (user, index) => (
                <a key={index} href="https://ec2-18-229-242-167.sa-east-1.compute.amazonaws.com" target="true" className="users-is-not-followeds__user">
                    <Image 
                        src={user.avatar || ImageNoFound}
                        avatar
                    />
                    <span>{user.username}</span>
                </a>
            ))}
        </div>
    )
}
