import React from 'react';
import { Form, Button } from 'semantic-ui-react';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import { toast } from 'react-toastify';
import { useMutation } from '@apollo/client';
import { UPDATE_USER } from '../../../gql/user'
import './WebSiteForm.scss';

export default function WebSite(props) {
    const { setShowModal, currentWebSite, refetch  } = props;

    const [ updateUser ] = useMutation(UPDATE_USER);

    const formik = useFormik({
        initialValues: {
            website: currentWebSite || ""
        },
        validationSchema: Yup.object({
            website: Yup.string().required(),
        }),
        onSubmit: async (formData) => {
            try {
                const result = await updateUser({
                    variables: {
                        input: formData
                    }
                })

                if(!result){
                    toast.error('Error al cambiar el sitio web');
                }else{
                    refetch();
                    setShowModal(false);
                    toast.success('Sitio Web cambiado con exito')
                }
            } catch (error) {
                toast.error('Error al cambiar el sitio web')
            }
        }
    });

    return (
        <Form className="website-form" onSubmit={formik.handleSubmit}>
            <Form.Input
                placeholder="Escriba un sitio web"
                name="website"
                value={formik.values.website}
                onChange={formik.handleChange}
                error={formik.errors.website && true}
            />

            <Button className="btn-submit">Cambiar Sitio Web</Button>
        </Form>
    );
}
