import React, {useEffect} from 'react';
import { Grid } from 'semantic-ui-react';
import { map } from 'lodash';
import { useQuery } from '@apollo/client';
import { GET_STORYS_FOLLOWERS } from '../../gql/story';
import PreviewStorys from '../Storys/PreviewStorys';
import './Storys.scss';


export default function Storys() {
    const { data, loading, startPolling, stopPolling } = useQuery(GET_STORYS_FOLLOWERS);

    useEffect(() => {
        startPolling(1000);
        return () => {
            stopPolling();
        }
    }, [startPolling, stopPolling]);

    if(loading) return null;
    const {getStorysFollowers} = data;

    return (
        <div>
            <h1>Historias</h1>
            <Grid columns={6}>
                <Grid.Row>
                    {map(getStorysFollowers, (story, index) => (
                        <Grid.Column key={index}>
                            <PreviewStorys story={story} />
                        </Grid.Column>
                    ))}
                </Grid.Row>
            </Grid>
        </div>
    )
}
