import React from 'react';
import { Form, Button } from 'semantic-ui-react';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import { toast } from 'react-toastify';
import { useMutation } from '@apollo/client';
import { UPDATE_USER } from '../../../gql/user';
import './DescriptionForm.scss';

export default function DescriptionForm(props) {
    const { setShowModal, currentDescription, refetch } = props;

    const [ updateUser ] = useMutation(UPDATE_USER)

    const formik = useFormik({
        initialValues: {
            description: currentDescription || "",
        },
        validationSchema: Yup.object({
            description: Yup.string().required(),
        }),
        onSubmit: async (formData) => {
            try {
                const result = await updateUser({
                    variables: {
                        input: formData
                    }
                })

                if(!result){
                    toast.error('Error al cambiar la Descripción');
                }else{
                    refetch();
                    setShowModal(false);
                    toast.success('Descripción cambiada con exito');
                }
            } catch (error) {
                toast.error('Error al cambiar la Descripción');
            }
        }
    });

    return (
        <Form className="description-form" onSubmit={formik.handleSubmit}>
            <Form.TextArea
                placeholder="Escribe una descripción"
                name="description"
                value={formik.values.description}
                onChange={formik.handleChange}
                className={formik.errors.description && "error"}
            />

            <Button className="btn-submit">Cambiar Descripción</Button>
        </Form>
    )
}
