import React from 'react';
import {Image} from 'semantic-ui-react';
import Stories from 'react-insta-stories';
import ImageNoFound from '../../../assets/png/avatar.png';
import './PreviewStorys.scss';

export default function PreviewStorys(props) {
    const {story} = props
    
    const storyModal = () => {
        console.log(story.file)
        return (
            <Stories
                stories={story.file}
                defaultInterval={1500}
                width={432}
                height={768}
            />
        );
    };

    return (
        <div className="preview-storys" onClick={storyModal}>
            <div className="preview-story">
                <div>
                    <Image className="img-avatar" src={story.idUser.avatar || ImageNoFound} avatar />
                </div>
                <div>
                    <Image className="img-story" src={story.file} />
                </div>
            </div>
        </div>
    )
}
