//layouts
import LayoutBasic from '../layouts/LayoutBasic';

//pages
import Home from '../pages/home';
import WeChat from '../pages/wechat';
import User from '../pages/User';
import Users from '../pages/Users';
import Storys from '../pages/storys';
import Error404 from '../pages/error404';

const routes = [
    {
        path: "/",
        layout: LayoutBasic,
        component: Home,
        exact: true,
    },
    {
        path: "/wechat",
        layout: LayoutBasic,
        component: WeChat,
        exact: true,
    },
    {
        path: "/storys",
        layout: LayoutBasic,
        component: Storys,
        exact: true,
    },
    {
        path: "/users",
        layout: LayoutBasic,
        component: Users,
        exact: true,
    },
    {
        path: "/:username",
        layout: LayoutBasic,
        component: User,
        exact: true,
    },
    {
        layout: LayoutBasic,
        component: Error404,
    }
];

export default routes;